import React from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";
import { useEffect, useState } from "react";
import axios from "axios";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
  text-align: center;
  // background-color: black;
  -ms-overflow-style: none;
  scrollbar-width: none;

  background-image: url(${process.env
    .PUBLIC_URL}/images/.png); /*     */
  background-size: cover; /*     */
  background-position: center; /*     */

  /*    */
  /* pc  390 */
  @media (hover: hover) {
    width: 390px;
    margin: 0 auto;
  }

  /*   */
  &::-webkit-scrollbar {
    display: none;
  }
`;
const BodyWrapper = styled.div`
  min-height: calc(100vh - 151px); //  
`;

const Body = styled.div``;

const Footer = styled.div`
  height: 150px;
  position: relative;
  border: none;
  margin: 0;
  // position: fixed; /*     */
  // bottom: 0; /*    */
  // background-color: black;
`;
const Left = styled.div`
  position: absolute;
  left: 17%;
  top: -3px;
`;
const FooterContentWrapper = styled.div`
  position: relative;
`;
const Base = styled.div``;
const FooterContent = styled.div`
  width: 300px;
  position: absolute;
  top: 28px;
  left: 50%;
  transform: translate(-50%);
  color: #fff;
  p {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
  }
  .bold {
    font-weight: 700;
  }
  #marginBottom {
    margin-bottom: 2px;
  }
`;
const ManagementWrapper = styled.div`
  display: flex;
  flex-direction: row;
  gap: 17px;
  justify-content: center;
`;
const FestivalWrapper = styled.div`
  margin-top: 25px;
`;
const DevelopmentWrapper = styled.div`
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  gap: 17px;
  justify-content: center;
`;
const Line = styled.div`
  height: 0px;
  margin-top: -6px;
`;
const Right = styled.div`
  position: absolute;
  right: 17%;
  top: -3px;
`;

const TopBar = styled.div`
  display: flex;
  justify-content: space-between;
  height: 70px;
  padding-left: 20px;
  box-sizing: border-box;
  margin-bottom: 5px;
  align-items: center;
`;

const Back = styled.div`
  width: 24px;
  cursor: pointer;
  position: absolute;
  left: 27px;
`;

const Title = styled.div`
  color: #fff;
  font-family: SUIT;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding-bottom: 5px;
  margin: auto;
`;

const None = styled.div`
  width: 24px;
  height: 24px;
`;

const TopBox = styled.div`
  position: relative;
  margin-top: 10px;
`;

const TopTitle = styled.div`
  color: #fff;
  font-weight: bold;
  position: relative;
  margin-top: -34px;
`;

const Check = styled.button`
  position: relative;
  padding: 14px 33px 13px 33px;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  border: 1px solid #d1fff7;
  background: rgba(134, 228, 212, 0.25);
  margin-top: 20px;

  color: #fff;
  font-family: SUIT;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
`;

const College = styled.select`
  padding: 11px 14px 11px 18px;
  border-radius: 10px;
  border: 1px solid #d1fff7;
  background: #fff;
  width: 285px;
  height: 70px;
  margin-top: 50px;
  outline: none;
  appearance: none;
  background: url("${process.env.PUBLIC_URL}/images/down-one.png") no-repeat
    right 14px center;
  background-color: white;
  background-size: 48px 48px; /*     */
`;

const InputBox = styled.div`
  width: 390px;
  margin: auto;
`;

const Input = styled.input`
  width: 261px;
  height: 70px;
  border-radius: 10px;
  border: 1px solid #d1fff7;
  background: #fff;
  margin-top: 20px;
  outline: none;
  padding-left: 22px;
`;

const Explain = styled.div`
  color: #fff;
  font-family: SUIT;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-top: 20px;
  margin-bottom: 20px;
`;

const Participation = styled.button`
  position: relative;
  padding: 14px 33px 13px 33px;
  border-radius: 10px;
  border: 1px solid #d1fff7;
  background: rgba(134, 228, 212, 0.25);

  color: #fff;
  font-family: SUIT;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin-bottom: 70px;
`;

const TopBox2 = styled.div`
  position: relative;
  margin: auto;
  width: 350px;
  margin-bottom: 5px;
`;
const ImgBox = styled.div`
  position: relative;
  left: -12.5px;
`;
const TopText = styled.div`
  position: relative;
  top: -98px;
  left: -3px;
`;

const RankNum1 = styled.div`
  position: absolute;
  color: #fff;
  font-family: SUIT;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  top: 0;
  left: 46px;
`;
const RankNum2 = styled.div`
  position: absolute;
  color: #fff;
  font-family: SUIT;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  top: 0;
  left: 45px;
`;
const RankNum3 = styled.div`
  position: absolute;
  color: #fff;
  font-family: SUIT;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  top: 0;
  left: 43px;
`;

const RankUniv = styled.div`
  position: absolute;
  left: 143px;
  top: 5px;
  color: #fff;
  font-family: SUIT;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
`;
const RankPercent = styled.div`
  position: absolute;
  margin-left: 290px;
  top: 4px;
  color: #fff;
  font-family: SUIT;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
`;

const Battle = () => {
  const navigate = useNavigate();
  const [selectedCollege, setSelectedCollege] = useState(""); //   
  const [stuId, setStuId] = useState(""); //  
  const container = {
    hidden: { opacity: 1, scale: 0 },
    visible: {
      opacity: 1,
      scale: 1,
      transition: {
        delayChildren: 0.3,
        staggerChildren: 0.2,
      },
    },
  };
  const item = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
    },
  };
  const ulStyle = {
    padding: "0",
    listStyle: "none",
  };
  const loadingimg = {
    margin: "0 auto",
    width: "10%",
    height: "10%",
  };
  const loadingStyle = {
    margin: "0 auto",
    display: "flex",
    justifyContent: "center",
    alignContent: "center",
    alignItems: "center",
    width: "100%",
    height: "100vh",
  };

  const onClickBtn = () => {
    navigate(-1); //    , '/main'     
  };

  const checkRanking = () => {
    navigate(`/CompetDetail`);
  };

  //   (  !!!)
  const [postList, setPostList] = useState([]); //   
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        // API 
        const response = await axios.get("http://127.0.0.1:8000/competition/");
        setPostList(response.data.collegeRank); //collegeRank  
      } catch (error) {
        console.error("Error fetching data:", error);
      }
      setLoading(false);
    };
    fetchData();
  }, []);

  const onParticipationClick = async () => {
    try {
      //     
      const response = await axios.post("http://127.0.0.1:8000/student-info/", {
        college: selectedCollege,
        stuId,
      });

      //   
      console.log(" :", response.data);
      navigate(`/CompetDetail`);

      // // POST      
      // if (response.status === 200 && response.data.success) {
      //   //  
      //   navigate(`/CompetDetail`);
      // }
    } catch (error) {
      //  
      console.error("  :", error);
      alert("  ");
    }
  };

  return (
    <Container>
      <TopBar>
        <Back onClick={onClickBtn}>
          <img
            src={`${process.env.PUBLIC_URL}/images/backbtn.png`}
            alt="back"
            width="24px"
          />
        </Back>
        <Title> </Title>
        <None></None>
      </TopBar>
      <BodyWrapper>
        {loading ? (
          <div style={loadingStyle}>
            <img
              src="/images/loading.gif"
              alt=" "
              width="50px"
              height="50px"
              style={loadingimg}
            />
          </div>
        ) : (
          <>
            <Body>
              <TopBox>
                <img
                  src={`${process.env.PUBLIC_URL}/images/Union.png`}
                  alt="back"
                  width="197.451px"
                  height="39.774px"
                />
                <TopTitle>  TOP 3 </TopTitle>
              </TopBox>
              <motion.ul
                variants={container}
                initial="hidden"
                animate="visible"
                style={ulStyle}
              >
                <motion.li variants={item}>
                  <TopBox2>
                    <ImgBox>
                      <img
                        src={`${process.env.PUBLIC_URL}/images/1stdept.svg`}
                      />
                    </ImgBox>
                    <TopText>
                      <RankNum1>{postList[0] && postList[0].rank}</RankNum1>
                      <RankUniv>{postList[0] && postList[0].college}</RankUniv>
                      <RankPercent>
                        {postList[0] && postList[0].participationRate}%
                      </RankPercent>
                    </TopText>
                  </TopBox2>
                </motion.li>
                <motion.li variants={item}>
                  <TopBox2>
                    <div>
                      <img
                        src={`${process.env.PUBLIC_URL}/images/2nddept.svg`}
                      />
                    </div>
                    <TopText>
                      <RankNum2>{postList[1] && postList[1].rank}</RankNum2>
                      <RankUniv>{postList[1] && postList[1].college}</RankUniv>
                      <RankPercent>
                        {postList[1] && postList[1].participationRate}%
                      </RankPercent>
                    </TopText>
                  </TopBox2>
                </motion.li>
                <motion.li variants={item}>
                  <TopBox2>
                    <div>
                      <img
                        src={`${process.env.PUBLIC_URL}/images/3rddept.svg`}
                      />
                    </div>
                    <TopText>
                      <RankNum3>{postList[2] && postList[2].rank}</RankNum3>
                      <RankUniv>{postList[2] && postList[2].college}</RankUniv>
                      <RankPercent>
                        {postList[2] && postList[2].participationRate}%
                      </RankPercent>
                    </TopText>
                  </TopBox2>
                </motion.li>
              </motion.ul>
              <Check onClick={checkRanking}> </Check>
              <InputBox>
                <College
                  value={selectedCollege}
                  onChange={(e) => setSelectedCollege(e.target.value)}
                >
                  <option value="" disabled selected>
                     
                  </option>
                  <option value="college1"></option>
                  <option value="college2"></option>
                  <option value="college3"></option>
                  <option value="college4"></option>
                  <option value="college5"></option>
                  <option value="college6"></option>
                  <option value="college7"></option>
                  <option value="college8"></option>
                  <option value="college9"></option>
                </College>
                <Input
                  type="text"
                  placeholder=""
                  id="number"
                  value={stuId}
                  onChange={(e) => setStuId(e.target.value)}
                ></Input>
              </InputBox>
              <Explain>
                    1  .
              </Explain>
              <Participation onClick={onParticipationClick}>
                
              </Participation>
            </Body>
          </>
        )}
      </BodyWrapper>
      <Footer>
        <Left>
          <img
            src={`${process.env.PUBLIC_URL}/images/footer-left.png`}
            width="55px"
            alt="footer"
          />
        </Left>
        <FooterContentWrapper>
          <Base>
            <img
              src={`${process.env.PUBLIC_URL}/images/footer-base.png`}
              width="100%"
              height="148px"
              alt="footer"
            />
          </Base>
          <FooterContent>
            <ManagementWrapper>
              <p className="bold">  </p>
              <p>   </p>
            </ManagementWrapper>
            <Line>
              <img
                src={`${process.env.PUBLIC_URL}/images/footer-line.png`}
                width="253px"
                alt="footer"
              />
            </Line>
            <FestivalWrapper>
              <p id="marginBottom">2023  </p>
              <p className="bold"> uniVERSE</p>
            </FestivalWrapper>
            <Line>
              <img
                src={`${process.env.PUBLIC_URL}/images/footer-line.png`}
                width="253px"
                alt="footer"
              />
            </Line>
            <DevelopmentWrapper>
              <p className="bold">  </p>
              <p> </p>
            </DevelopmentWrapper>
          </FooterContent>
        </FooterContentWrapper>
        <Right>
          <img
            src={`${process.env.PUBLIC_URL}/images/footer-right.png`}
            width="55px"
            alt="footer"
          />
        </Right>
      </Footer>
    </Container>
  );
};
export default Battle;
