import React from "react";
import styled from "styled-components";
import { useNavigate, useParams } from "react-router-dom";
import { motion } from "framer-motion";
import axios from "axios";
import { useState, useEffect } from "react"; // useState useEffect 
const Container = styled.div`
display: flex;
flex-direction: column;
min-height: 100vh;
position: relative;
text-align: center;\
background-color: #000204;

/*    */
/* pc  390 */
@media (hover: hover) {
  width: 390px;
  margin: 0 auto;
}

/*   */
&::-webkit-scrollbar {
  display: none;
}
`;
const BodyWrapper = styled.div`
  min-height: calc(100vh - 150px); //  
`;

const Body = styled.div`
  align-items: center;
`;
const Topbar = styled.div`
  display: flex;
  justify-content: space-between;
  height: 70px;
  align-items: center;
  padding-left: 12px;
`;
const Back = styled.div`
  cursor: pointer;
  position: absolute;
  left: 27px;
`;
const BoothCon = styled.div``;
const Boothimg = styled.div`
  margin: auto 0;
  padding-bottom: 20px;
`;
const Boothinfo = styled.div`
  margin: 0 auto;
  width: 300px;
  margin-bottom: 50px;
`;
const Boothname = styled.div`
  width: 310px;
  height: 100%;
  flex-shrink: 0;
  color: #4fdfff;
  font-family: SUIT;
  font-size: 27.089px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding-bottom: 15px;
  text-align: left;
`;

const Intro = styled.div`
  width: 310px;
  height: 120px;
  flex-shrink: 0;
  color: #fff;
  font-family: SUIT;
  font-size: 18.059px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: left;
  margin-top: 40px;
  overflow: hidden; /*       */
`;
const BoothintroContent = styled.div`
  height: 100%;
  overflow-y: auto; /*    */
`;
const Detail_detail = styled.div`
  margin-left: 10px;
  margin-top: 30px;
`;
const Time = styled.div`
  width: 310px;
  height: 34.614px;
  flex-shrink: 0;
  color: #fff;
  font-family: SUIT;
  font-size: 18.059px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: left;
  display: flex;
`;
const Time_detail = styled.div`
  height: 22px;
  height: 22px;
  width: 87%;
  flex-shrink: 0;
  color: #4fdfff;
  font-family: SUIT;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-left: 10px;
  /* margin-top: -2px; */
  /* margin-bottom: 10px;
`;
const Place = styled.div`
  height: 34.614px;
  flex-shrink: 0;
  color: #fff;
  font-family: SUIT;
  font-size: 18.059px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: left;
  display: flex;
  margin-top: 10px;
`;
const Place_detail = styled.div`
  width: 310px;
  height: 34px;
  flex-shrink: 0;
  color: #4fdfff;
  font-family: SUIT;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-left: 10px;
  margin-top: -2px;
`;
const Footer = styled.div`
  height: 150px;
  position: relative;
`;
const Left = styled.div`
  position: absolute;
  left: 17%;
  top: -3px;
`;
const FooterContentWrapper = styled.div`
  position: relative;
`;
const Base = styled.div``;
const FooterContent = styled.div`
  width: 300px;
  position: absolute;
  top: 28px;
  left: 50%;
  transform: translate(-50%);
  color: #fff;
  p {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
  }
  .bold {
    font-weight: 700;
  }
  #marginBottom {
    margin-bottom: 2px;
  }
`;
const ManagementWrapper = styled.div`
  display: flex;
  flex-direction: row;
  gap: 17px;
  justify-content: center;
`;
const FestivalWrapper = styled.div`
  margin-top: 25px;
`;
const DevelopmentWrapper = styled.div`
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  gap: 17px;
  justify-content: center;
`;
const Line = styled.div`
  height: 0px;
  margin-top: -6px;
`;
const Right = styled.div`
  position: absolute;
  right: 17%;
  top: -3px;
`;

const Boothdetail = () => {
  const navigate = useNavigate();
  const navigateToBack = () => {
    navigate(-1);
  };

  const loadingimg = {
    margin: "0 auto",
    width: "10%",
    height: "10%",
  };
  const loadingStyle = {
    margin: "0 auto",
    display: "flex",
    justifyContent: "center",
    alignContent: "center",
    alignItems: "center",
    width: "100%",
    height: "100vh",
  };

  //axios_backend  
  const [Booth, setBooth] = useState([]);
  const { boothId } = useParams(); //    
  const BACKEND_URL = "http://15.165.137.211" || "https://15.165.137.211" || "http://127.0.0.1:8000";
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const apiUrl = `${BACKEND_URL}/booth-detail/${boothId}/`;

    axios
      .get(apiUrl)
      .then((response) => {
        setBooth(response.data);
        console.log(response.data); //   
        setLoading(false);
      })
      .catch((error) => {
        console.error(" : ", error);
        setLoading(false);
      });
  }, [boothId]);
  return (
    //      
    <motion.div
      initial={{ scale: 0.5 }}
      animate={{ scale: 1 }}
      transition={{
        type: "spring",
        stiffness: 260,
        damping: 20,
      }}
    >
      <Container>
        <BodyWrapper>
          <Body>
            <Topbar>
              <Back>
                <img
                  src={`${process.env.PUBLIC_URL}/images/backbtn.png`}
                  width="24px"
                  height="24px"
                  onClick={() => navigateToBack()}
                />
              </Back>
            </Topbar>
            {loading ? (
              <div style={loadingStyle}>
                <img
                  src="/images/loading.gif"
                  alt=" "
                  width="50px"
                  height="50px"
                  style={loadingimg}
                />
              </div>
            ) : (
              <>
                <BoothCon>
                  <Boothimg>
                    <img
                      src={`${BACKEND_URL}${Booth.image}`}
                      width="320px"
                      height="320px"
                      alt="boothimg"
                    />
                  </Boothimg>
                  <Boothinfo>
                    <Boothname>{Booth.name}</Boothname>
                    <Intro>
                      <BoothintroContent> {Booth.introduce}</BoothintroContent>
                    </Intro>
                    <Detail_detail>
                      <Time>
                        <img
                          src={`${process.env.PUBLIC_URL}/images/timeicon.png`}
                          width="14px"
                          height="17px"
                          alt="timeicon"
                        />
                        <Time_detail>{Booth.date}</Time_detail>
                      </Time>
                      <Place>
                        <img
                          src={`${process.env.PUBLIC_URL}/images/placeicon.png`}
                          width="13.993px"
                          height="17px"
                          alt="placeicon"
                        />
                        <Place_detail>{Booth.place}</Place_detail>
                      </Place>
                    </Detail_detail>
                  </Boothinfo>
                </BoothCon>
              </>
            )}
          </Body>
        </BodyWrapper>
        <Footer>
          <Left>
            <img
              src={`${process.env.PUBLIC_URL}/images/footer-left.png`}
              width="55px"
              alt="footer"
            />
          </Left>
          <FooterContentWrapper>
            <Base>
              <img
                src={`${process.env.PUBLIC_URL}/images/footer-base.png`}
                width="100%"
                height="148px"
                alt="footer"
              />
            </Base>
            <FooterContent>
              <ManagementWrapper>
                <p className="bold">  </p>
                <p>   </p>
              </ManagementWrapper>
              <Line>
                <img
                  src={`${process.env.PUBLIC_URL}/images/footer-line.png`}
                  width="253px"
                  alt="footer"
                />
              </Line>
              <FestivalWrapper>
                <p id="marginBottom">2023  </p>
                <p className="bold"> uniVERSE</p>
              </FestivalWrapper>
              <Line>
                <img
                  src={`${process.env.PUBLIC_URL}/images/footer-line.png`}
                  width="253px"
                  alt="footer"
                />
              </Line>
              <DevelopmentWrapper>
                <p className="bold">  </p>
                <p> </p>
              </DevelopmentWrapper>
            </FooterContent>
          </FooterContentWrapper>
          <Right>
            <img
              src={`${process.env.PUBLIC_URL}/images/footer-right.png`}
              width="55px"
              alt="footer"
            />
          </Right>
        </Footer>
      </Container>
    </motion.div>
  );
};
export default Boothdetail;
