import React from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";

const Container = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  position: relative;
  text-align: center;
  background: var(--purple-4, #0a0047);
  -ms-overflow-style: none;
  scrollbar-width: none;

  /*    */
  /* pc  390 */
  @media (hover: hover) {
    width: 390px;
    margin: 0 auto;
  }

  /*   */
  &::-webkit-scrollbar {
    display: none;
  }
`;
// 
const Topbar = styled.div`
  display: flex;
  justify-content: space-between;
  height: 70px;
  margin-bottom: 5px;
  box-sizing: border-box;
  align-items: center;
  padding-left: 20px;
`;
const Back = styled.div`
  cursor: pointer;
  position: absolute;
  left: 27px;
`;
const Toptitle = styled.div`
  color: #fff;
  cursor: pointer;
  margin: auto;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  padding-bottom: 5px;
`;

const BodyWrapper = styled.div`
  min-height: calc(100vh - 151px); //  
`;

const TopIndex = styled.div`
  position: absolute;
  margin-left: 3%;
  left: -2.8%;
  width: 100%;
  height: 40px;
  display: flex;

  color: #0a0047;
  font-family: SUIT;
  font-size: 13px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
`;
const Index1 = styled.span`
  flex: 0.8;
  padding: 4px;
`;
const Index2 = styled.span`
  flex: 0.6;
  padding: 4px;

  color: #f5e889;
  font-family: SUIT;
  font-size: 13px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;

  text-shadow: 0.3px 0.3px 0px #0a0047;
`;
const Index3 = styled.span`
  flex: 0.8;
  padding: 3px;
  text-shadow: 0.3px 0.3px 0px #0a0047;
`;
const NameCode = styled.div`
  color: #fff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: SUIT;
  font-size: 18.5px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
`;

const InstagramBox = styled.div`
  position: absolute;
  left: 11%;

  top: 17.6%;
  width: 100%;
  height: 20px;
`;
const TopBox = styled.div`
  position: absolute;
  text-align: center;
  top: 25%;
  left: 3%;
  width: 94%;
  height: 21.6%;
`;

const ContentBox = styled.div`
  position: absolute;
  top: 61.9%;
  left: 6.2%;
  width: 87%;
  height: 30%;

  color: #fff;
  font-family: SUIT;
  font-size: 10.3px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
`;

const C1 = styled.span`
  color: #f5e889;
  text-align: center;
  font-family: SUIT;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
`;
const C2 = styled.span``;
const C3 = styled.p``;

//
const Footer = styled.div`
  margin-top: 20px;
  height: 150px;
  position: relative;
`;
const Left = styled.div`
  position: absolute;
  left: 17%;
  top: -3px;
`;
const FooterContentWrapper = styled.div`
  position: relative;
`;
const Base = styled.div``;
const FooterContent = styled.div`
  width: 300px;
  position: absolute;
  top: 28px;
  left: 50%;
  transform: translate(-50%);
  color: #fff;
  p {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
  }
  .bold {
    font-weight: 700;
  }
  #marginBottom {
    margin-bottom: 2px;
  }
`;
const ManagementWrapper = styled.div`
  display: flex;
  flex-direction: row;
  gap: 17px;
  justify-content: center;
`;
const FestivalWrapper = styled.div`
  margin-top: 25px;
`;
const DevelopmentWrapper = styled.div`
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  gap: 17px;
  justify-content: center;
`;
const Line = styled.div`
  height: 0px;
  margin-top: -6px;
`;
const Right = styled.div`
  position: absolute;
  right: 17%;
  top: -3px;
`;

const IntroCommittee = () => {
  const navigate = useNavigate();
  const GoBack = () => {
    navigate("/");
  };

  const GoFestival = () => {
    //      .
    navigate("/Festival");
  };
  const GoLion = () => {
    //      .
    navigate("/IntroLion");
  };

  const imageStyle = {
    marginRight: "32%",
    marginLeft: "10px;",
  };

  const instagramText = {
    marginRight: "10px",
    color: "#F5E889",
    fontFamily: "Inter",
    fontSize: "12px",
    fontStyle: "normal",
    fontWeight: "500",
    lineHeight: "normal",
  };

  const linkStyle = {
    color: "#FFF",
    fontFamily: "Inter",
    fontSize: "12px",
    fontStyle: "normal",
    fontWeight: "400",
    lineHeight: "normal",
  };

  return (
    <Container>
      <Topbar>
        <Back>
          <img
            src={`${process.env.PUBLIC_URL}/images/backbtn.png`}
            width="24px"
            height="24px"
            onClick={() => GoBack()}
          />
        </Back>
        <Toptitle></Toptitle>
      </Topbar>
      <motion.div
        initial={{ scale: 0.8 }}
        animate={{ scale: 1 }}
        transition={{
          type: "spring",
          stiffness: 260,
          damping: 20,
        }}
      >
        <BodyWrapper>
          <div style={{ margin: "0px auto", position: "relative" }}>
            <TopIndex>
              <Index1 onClick={GoFestival}></Index1>
              <Index2>..</Index2>
              <Index3 onClick={GoLion}> 11</Index3>
            </TopIndex>

            <NameCode
              style={{
                top: "7.7%", //absolute  top %
                position: "absolute",
                left: "14%",
                // transform: "translateX(-30%)",
              }}
            >
               
            </NameCode>

            <InstagramBox>
              <span style={instagramText}>instagram</span>
              <a
                style={linkStyle}
                href="https://instagram.com/ddwu.festival2023?igshid=MzRlODBiNWFlZA=="
                target="_blank"
              >
                @ddwu.festival2023
              </a>
            </InstagramBox>

            <TopBox>
              <img
                src={`${process.env.PUBLIC_URL}/images/CommitLogo.png`}
                width="73%"
                style={{ marginTop: "0%" }}
              />
            </TopBox>

            <ContentBox>
              <C1></C1>
              <C2></C2>
              <C3>
                        
                <br />
                .
                <C3>
                  {" "}
                  <strong>
                    , , , , , 
                  </strong>
                  <br />
                    ,
                  <br /> <strong>37 </strong> 3  
                  .
                </C3>
                <C3>
                       
                  <br />     .
                  <br />
                  <strong></strong>   
                   
                  <br />
                   .
                  <br />  4    
                  
                  <br />      
                  ,
                  <br /> 5    
                  <br />      
                   
                  <br />
                  .
                </C3>
                <br />  3     
                
                <br />   VLOG     
                !
              </C3>
            </ContentBox>

            <img
              src={`${process.env.PUBLIC_URL}/images/FrameCommittee.png`}
              style={{ maxWidth: "88%", height: "auto" }}
            />
          </div>
        </BodyWrapper>
      </motion.div>
      <Footer>
        <Left>
          <img
            src={`${process.env.PUBLIC_URL}/images/footer-left.png`}
            width="55px"
            alt="footer"
          />
        </Left>
        <FooterContentWrapper>
          <Base>
            <img
              src={`${process.env.PUBLIC_URL}/images/footer-base.png`}
              width="100%"
              height="148px"
              alt="footer"
            />
          </Base>
          <FooterContent>
            <ManagementWrapper>
              <p className="bold">  </p>
              <p>   </p>
            </ManagementWrapper>
            <Line>
              <img
                src={`${process.env.PUBLIC_URL}/images/footer-line.png`}
                width="253px"
                alt="footer"
              />
            </Line>
            <FestivalWrapper>
              <p id="marginBottom">2023  </p>
              <p className="bold"> uniVERSE</p>
            </FestivalWrapper>
            <Line>
              <img
                src={`${process.env.PUBLIC_URL}/images/footer-line.png`}
                width="253px"
                alt="footer"
              />
            </Line>
            <DevelopmentWrapper>
              <p className="bold">  </p>
              <p> </p>
            </DevelopmentWrapper>
          </FooterContent>
        </FooterContentWrapper>
        <Right>
          <img
            src={`${process.env.PUBLIC_URL}/images/footer-right.png`}
            width="55px"
            alt="footer"
          />
        </Right>
      </Footer>
    </Container>
  );
};

export default IntroCommittee;
